<template>
    <div class="step" v-if="orderInfo">
        <div v-if="orderInfo.orderType === 2">
            <!--拼购状态-->
            <div class="content-top" v-if="orderInfo.pStatus === -1 || orderInfo.pStatus === 3">
                <p class="text">{{$L('团单已失效')}}</p>
                <img class="img" src="../../../assets/img/order/4.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.pStatus === 1 || orderInfo.pStatus === 2">
                <p class="text">{{$L('凑单中')}}</p>
                <img class="img" src="../../../assets/img/order/10.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.pStatus === 4">
                <p class="text">{{$L('拼团已成功，等待卖家发货咯！')}}</p>
                <img class="img" src="../../../assets/img/order/8.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.pStatus === 5">
                <p class="text">{{$L('卖家已发货咯！')}}</p>
                <img class="img" src="../../../assets/img/order/2.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.pStatus === 6">
                <p class="text">{{$L('拼购已完成，请确认收货咯！')}}</p>
                <img class="img" src="../../../assets/img/order/3.png" alt="">
            </div>
        </div>
        <div v-else>
            <div class="content-top" v-if="orderInfo.thisOrderStatus < 0 || orderInfo.thisOrderStatus === 7 ">
                <p class="text">{{$L('订单已关闭')}}</p>
                <img class="img" src="../../../assets/img/order/4.png" alt="">
            </div>
            <div class="content-top" v-if="orderInfo.thisOrderStatus === 6">
                <p class="text">{{$L('订单待发货')}}</p>
                <img class="img" src="../../../assets/img/order/4.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus < 2">
                <p class="text" v-if="orderInfo.payStatus === -1">{{$L('货到付款')}}</p>
                <p class="text" v-else-if="orderInfo.payStatus === -2">{{$L('到店付款')}}</p>
                <p class="text" v-else-if="orderInfo.payStatus === -3">{{$L('等待买家汇款')}}</p>
                <p class="text" v-else>{{$L('等待买家付款')}}</p>
                <img class="img" src="../../../assets/img/order/9.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus === 2">
                <p class="text">{{$L('卖家已发货')}}</p>
                <img class="img" src="../../../assets/img/order/2.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus ===4 && orderInfo.refundStatus ===0">
                <p class="text">{{$L('退款成功')}}</p>
                <img class="img" src="../../../assets/img/order/6.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus === 3 && orderInfo.refundStatus ===3">
                <p class="text">{{$L('售后完成')}}</p>
                <img class="img" src="../../../assets/img/order/7.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus === 3 && orderInfo.refundStatus ===2">
                <p class="text">{{$L('售后中')}}</p>
                <img class="img" src="../../../assets/img/order/5.png" alt="">
            </div>
            <div class="content-top" v-else-if="orderInfo.thisOrderStatus === 4 || orderInfo.thisOrderStatus === 5 || orderInfo.thisOrderStatus === 10">
                <p class="text">{{$L('已完成')}}</p>
                <img class="img" src="../../../assets/img/order/3.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "step",
        props:{
            orderInfo:{
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
    }
</script>

<style lang="scss" scoped>
    .step{
        .content-top {
            background: url('../../../assets/img/order/bgc.png') no-repeat center;
            background-size: cover;
            height: 120px;
            position: relative;
            .text{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 30px;
                font-size: 18px;
                color: #fff;
            }
            .img{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 30px;
            }
        }
    }
</style>