<template>
    <div class="order-info" v-loading="loading" v-if="orderInfo">
        <step :order-info="orderInfo"></step>
        <div class="order-message" v-if="orderInfo.orderType === 1 || orderInfo.orderType === 2">
            <p class="title">{{ $L('订单信息') }}</p>
            <order-detail v-bind:orderInfo="orderInfo"></order-detail>
        </div>
        <div class="logistics-message" v-if="orderInfo.orderType === 1 || orderInfo.orderType === 2">
            <p class="title">{{ $L('物流信息') }}</p>
            <div v-if="deliveryList.length==0">
                <div class="deliver">
                    {{ $L('暂时未有物流信息') }}
                </div>
            </div>
            <div v-if="deliveryList.length>0">
                <div class="order-number" v-if="orderInfo.deliveryName != '' || orderInfo.deliveryNum != ''">
                    {{ orderInfo.deliveryName }} {{ orderInfo.deliveryNum }}
                </div>
                <logistics-info v-bind:deliveryList="deliveryList"></logistics-info>
            </div>
        </div>
        <div v-if="orderInfo.adminMemo">
            <div class="leave-msg" v-if="orderInfo.orderType == 3">
                <p class="title">{{ $L('商家留言') }}</p>
                <p>{{ orderInfo.adminMemo ? orderInfo.adminMemo : $L('无') }}</p>
            </div>
        </div>
        <div class="prod-info">
            <div class="prod-item" v-for="item in orderInfo.getProductInfo">
                <img class="left img" :src="item.smallImg" alt="">
                <div class="left text">
                    <p class="chr-name">{{item.chrName}}</p>
                    <p>{{item.spec}}</p>
                </div>
                <div class="right num">
                    <p>{{item.goodsPrice}}</p>
                    <p>×{{item.goodsNumber}}</p>
                </div>
            </div>
            <div class="spell" v-if="orderInfo.orderType == 2">
                <div class="end" v-if="orderInfo.pStatus === -1">
                    {{$L('拼单已结束，请下次参与！')}}
                </div>
                <div v-if="orderInfo.remaining > 0 && orderInfo.pStatus === 1">
                    <div class="title"> {{$L('限时凑单：离结束还有')}} <span class="num">{{d}}</span> {{ $L('天')}} <span class="num">{{h}}</span> {{ $L('时')}} <span class="num">{{m}}</span> {{ $L('分')}} <span class="num">{{s}}</span> {{ $L('秒')}}</div>
                    <ul class="ul">
                        <li class="li" v-for="item in orderInfo.exuserHead">
                            <img :src="item" alt="" class="img">
                        </li>
                        <li class="text">
                            {{orderInfo.peopleNum}}{{$L('人团：')}}<br>
                            {{ $L('还差')}}{{orderInfo.peopleNum - orderInfo.exuserHead.length}}{{$L('人成团')}}
                        </li>
                    </ul>
                </div>
                <div v-if="orderInfo.pStatus === 4">
                    {{$L('拼团已成功，等待卖家发货咯！')}}
                </div>
                <div v-if="orderInfo.pStatus === 5">
                    {{$L('拼团已完成，卖家已经发货了！')}}
                </div>
                <div v-if="orderInfo.pStatus === 6">
                    {{$L('拼购已完成，请确认收货咯！')}}
                </div>
            </div>
            <ul class="ul">
                <li class="li"><span class="left">{{ $L('商品总额') }}:</span><span class="right">{{ orderInfo.goodsAmount }}</span></li>
                <li class="li"><span class="left">{{ $L('店铺优惠') }}:</span><span class="right">-{{ orderInfo.discountMoney }}</span></li>
                <li class="li"><span class="left">{{ $L('包装费用') }}:</span><span class="right">{{ orderInfo.packFee }}</span></li>
                <li class="li"><span class="left">{{ $L('运费') }}:</span><span class="right">{{ orderInfo.deliveryFee }}</span></li>
                <li class="li"><span class="left">{{ $L('支付手续费') }}:</span><span class="right">{{ orderInfo.payFee }}</span></li>
                <li class="li"><span class="left">{{ $L('发票税额') }}:</span><span class="right">{{ orderInfo.taxFee }}</span></li>
                <li class="li"><span class="left font-black">{{ $L('实付金额') }}:</span><span class="font-red right">{{ orderInfo.orderAmount }}</span></li>
            </ul>
            <div class="order-no">
                <p><span class="tit">{{ $L('订单编号') }}：</span>{{ orderInfo.orderSn }}</p>
                <p><span class="tit">{{ $L('下单时间') }}：</span>{{ orderInfo.addTime }}</p>
            </div>

        </div>
        <bottom-btn :order-info="orderInfo"></bottom-btn>
    </div>
</template>

<script>
    import OrderDetail from "../../../components/order/orderDetail";
    import LogisticsInfo from "../../../components/order/LogisticsInfo";
    import ProdList from "../../../components/order/prodList";
    import Step from "../../../components/mobile/order/step";
    import BottomBtn from "../../../components/order/bottomBtn";
    import orderPay from "./orderPay.vue";

    export default {
        name: "orderInfo",
        components: {ProdList, LogisticsInfo, OrderDetail,Step,BottomBtn},
        data() {
            return {
                loading: true,
                orderInfo: {},
                deliveryList:[],
                d:'',
                h:'',
                m:'',
                s:'',
            }
        },
        computed:{

        },
        created() {
            let id = 0;
            if (this.$route.params.hasOwnProperty('id')) {
                id = this.$route.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
                this.countTime();
            } else {
                this.showError();
            }
        },
        methods: {
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            showError() {
                this.$alert(this.$L('订单不存在或已删除'), this.$L('参数有误'), {
                    confirmButtonText: this.$L('返回'),
                    type: 'error',
                }).then(() => {
                    this.backFunc();
                });
            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                _self.$api('orderDetail', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.orderInfo = reData.data;
                        _self.deliveryList = reData.data.deliveryList;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(id);
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
            changeOrderType(item, status) {
                switch (status) {
                    case 'del':
                        //删除订单
                        if (item.thisOrderStatus < 0) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'del'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        //删除后返回列表
                                        this.$router.push({name:'orderList'});
                                    } else {

                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'refund':
                        if (item.hasOwnProperty('refundStatus') && item.refundStatus === 1) {
                            //申请售后
                            this.$router.push({name: "orderService", params: {id: item.orderId}});
                            break;
                        }
                        break;
                    case 'pay':
                        //立即付款
                        if ((item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus >= 0) {
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '100%',
                                loading: true,
                                data:{
                                    orderId: item.orderId,
                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.thisOrderStatus === 0) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'cancel'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'finish':
                        //确认收货
                        if (item.thisOrderStatus === 2) {
                            this.$confirm(this.$L('是否确认收货？'), this.$L('确认收货'), {
                                confirmButtonText: this.$L('立即确认'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'finish'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.commentsStatus === 0) {
                            this.$router.push({name: "orderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'afterSaleInfo':
                        //售后详情
                        if (item.thisOrderStatus === 3 || item.thisOrderStatus === 4) {
                            this.$router.push({name: "orderServiceInfo", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //评价详情
                        if (item.commentsStatus === 1) {
                            this.$router.push({name: "checkComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            gotoGoodsPage(item) {

            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
            reloadData() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            countTime(){
                let date = new Date();
                let now = date.getTime();
                //定义变量 d,h,m,s保存倒计时的时间
                this.orderInfo.remaining = parseInt(this.orderInfo.remaining);
                if (this.orderInfo.remaining > 0) {
                    this.d = Math.floor(this.orderInfo.remaining / (60 * 60 * 24));
                    this.h = Math.floor(this.orderInfo.remaining / (60 * 60)) - (this.d * 24);
                    this.m = Math.floor(this.orderInfo.remaining / 60) - (this.d * 24 * 60) - (this.h * 60);
                    this.s = Math.floor(this.orderInfo.remaining) - (this.d * 24 * 60 * 60) - (this.h * 60 * 60) - (this.m * 60);
                    this.h = this.h < 10 ? ('0' + this.h) : this.h;
                    this.m = this.m < 10 ? ('0' + this.m) : this.m;
                    this.s = this.s < 10 ? ('0' + this.s) : this.s;
                }
                this.orderInfo.remaining --;
                //递归每秒调用countTime方法，显示动态时间效果
                setTimeout(this.countTime, 1000);
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";

    .order-info {
        position: relative;
        width: 100%;
        min-height: 500px;
        .title {
            font-weight: 700;
            margin-bottom: 10px;
            font-size: 14px;
        }
        .order-message{
            background: #fff;
            padding: 20px 10px 0;
            vertical-align: top;
            margin-bottom: 10px;
            /deep/ .order-detail{
                padding-left: 0;
                .tr{
                    .left-td{
                        width: 67px;
                    }
                }
            }
        }
        .logistics-message{
            background: #fff;
            padding: 10px;
            vertical-align: top;
            .logistics-info{
                height: 230px;
                overflow-y: scroll;
            }
            .order-number{
                margin-bottom: 10px;
                line-height: 42px;
            }
        }
        .leave-msg{
            background: #fff;
            padding: 10px;
            margin: 10px 0;
        }
        .prod-info {
            background-color: #fff;
            margin-top: 10px;
            padding: 0 10px;
            .prod-item{
                padding: 10px 0;
                overflow: hidden;
                border-bottom: 1px solid #E1E1E1;
                .img{
                    height: 80px;
                    width: 80px;
                }
                .text{
                    display: inline-block;
                    vertical-align: top;
                    margin-left: 10px;
                    width: 40%;
                    .chr-name{
                        word-wrap:break-word
                    }
                }
                .num{
                    text-align: right;
                }
            }
            .spell{
                margin-top: 10px;
                background-color: #F2F2F2;
                padding: 10px;
                .title{
                    font-size: 14px;
                    .num{
                        background-color: #CC0000;
                        color: #ffffff;
                        padding: 3px;
                    }
                }
                .ul{
                    display: flex;
                    justify-content: flex-start;
                    padding: 10px 0 0;
                    .li{
                        height: 50px;
                        width: 50px;
                        margin-right: 10px;
                        background-color: dodgerblue;
                        border-radius: 50%;
                        .img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .text{
                        margin-top: 0;
                        font-size: 12px;
                    }
                }
                .end{
                    line-height: 50px;
                    font-size: 16px;
                }
            }
            .ul{
                padding: 15px 0;
                line-height: 24px;
                font-size: 14px;
                .li{
                    overflow: hidden;
                    &:last-child{
                        padding: 20px 0 0;
                        .font-black{
                            font-weight: 700;
                        }
                        .font-red{
                            color: #FF0000;
                            font-size: 20px;
                        }
                    }
                }
            }
            .order-no{
                border-top: 1px solid #E1E1E1;
                border-bottom: 1px solid #E1E1E1;
                color: #666666;
                font-size: 14px;
                padding: 10px 0;
            }
        }
        /deep/ .bottom-btn {
            padding: 20px 0;
            background: #fff;
            text-align: right;
            .el-button {
                width: 80px;
            }
        }
        .left{
            float: left;
        }
        .right{
            float: right;
        }
    }
</style>
